import { Typography } from '@material-ui/core'
import { Cancel } from '@material-ui/icons'
import React from 'react'
import {
  BooleanInput,
  DateTimeInput, Edit,
  EditProps, Labeled, SaveButton, SelectInput, ShowButton, SimpleForm,
  TextField,
  TextInput, TitleProps,
  Toolbar,
  ToolbarProps, useEditContext, useTranslate
} from 'react-admin'
import { EngineDto } from '../../../core/dto/device/engine/engine.dto'
import { DeviceDirection } from '../../../core/enum/DeviceDirection'
import { Multiplicities } from '../../../core/enum/Multiplicities'

interface EngineTitleProps extends TitleProps {
  record?: EngineDto
}

const EditTitle = (data: EngineTitleProps) => {
  const {
    record,
  } = data
  const translate = useTranslate()
  return (
    <span>
      {translate('resources.engines.titles.edit')}: {record?.id}. {record?.name}{' '}
    </span>
  )
}

const HidingField = ({ source }: { source: keyof EngineDto }) => {
  const { record } = useEditContext<EngineDto>()
  return (
    <>
      {record && record[source] && (
        <Labeled label="resources.engines.fields.disabled">
          <Typography component="span" color="textPrimary">
            {record[source]}
          </Typography>
        </Labeled>
      )}
    </>
  )
}

const EditToolbar = (props: ToolbarProps) => (
  <Toolbar {...props}>
    <SaveButton />
    <ShowButton icon={<Cancel />} label="const.cancel" />
  </Toolbar>
)

export const EngineEdit = (props: EditProps) => (
  <Edit {...props} title={<EditTitle />}>
    <SimpleForm redirect="show" toolbar={<EditToolbar />}>
      <TextField source="id" />
      <TextInput source="name" />
      <BooleanInput source="isWatched" />
      <DateTimeInput source="lastRefresh" />
      <TextInput source="address" />
      <SelectInput
        source="direction"
        choices={[
          { id: DeviceDirection.IN, name: 'IN' },
          { id: DeviceDirection.INFO, name: 'INFO' },
          { id: DeviceDirection.OUT, name: 'OUT' },
        ]}
      />
      <TextInput source="categoryId" />
      <TextField source="stanbyMode" />
      <HidingField source="disabled" />
      <BooleanInput source="onlineInfixCeck" />
      <SelectInput
        source="multiplicity"
        choices={[
          { id: Multiplicities.CHECK_ALL, name: 'CHECK_ALL' },
          { id: Multiplicities.MULTIPLE, name: 'MULTIPLE' },
          { id: Multiplicities.SINGLE, name: 'SINGLE' },
        ]}
      />
    </SimpleForm>
  </Edit>
)
